.App {
  font-family: sans-serif;
  text-align: center;
  margin-left: 16px;
}

.display {
  width: 180px;
  border: 1px solid #666666;
  border-radius: 4px;
  margin: 8px 0px;
  height: 100px;
  text-align: right;
}
.keyboard {
  width: 200px;
  display: flex;
  flex-wrap: wrap;
}

.keyboard > button {
  width: 40px;
  padding: 12px 8px;
  margin: 3px;
  cursor: pointer;
}
